@import url('https://fonts.googleapis.com/css2?family=Outfit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
    scroll-behavior: smooth;
}
body{
    background:#F5F5F5;
}
a{
    color: inherit;
    line-height: 1;
    cursor: pointer;
    text-decoration: none;
}
.container{
    padding-left: 10%;
    padding-right: 10%;
}
.btn{
    outline: none;
    border: none;
    background:#fff;
    color: #212121;
    padding: 14px 25px;
    font-size: 16px;
    border-radius: 30px;
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}
.btn img{
    width: 20px;
    margin-left: 10px;
}
.btn.dark-btn{
background: #212EA0;
color: #fff;
}

@media(max-width:1200px)
{
    .container{
        padding-left: 5%;
        padding-right: 5%;
    }
    .btn{
        
        padding: 14px 22px;
    }
}